import API from '../../api';
import { SITES } from '../../api/endpoints';

export async function getSites() {
  const api = new API();
  return api.get(SITES).then(
    res => res.data,
    error => {
      throw error;
    }
  );
}

export async function getSite(id) {
  const api = new API();
  return api.get(SITES, id).then(
    res => res.data,
    error => {
      throw error;
    }
  );
}

export async function addSite(site) {
  const api = new API();
  return api.post(SITES, site).then(
    res => res.data,
    error => {
      throw error;
    }
  );
}

export async function addLevel(siteId, level) {
  const api = new API({
    headers: { contentType: 'multipart/form-data' },
  });

  const formData = new FormData();

  formData.append('plan', level.plan);
  formData.append('name', level.name);
  formData.append('image', level.image);

  return api.post(`${SITES}/${siteId}/addLevel`, formData).then(
    res => res.data,
    error => {
      throw error;
    }
  );
}

export async function deleteLevelFromSite(siteId, levelId) {
  const api = new API();
  return api.delete(`${SITES}/${siteId}/addLevel`, levelId).then(
    res => res.data,
    error => {
      throw error;
    }
  );
}

export async function addCameraToSite(siteId, camera, level, cameraPoints, floorPlanPoints) {
  const api = new API();
  return api
    .post(`${SITES}/${siteId}/addCamera`, {
      siteId,
      camera,
      level,
      cameraPoints,
      floorPlanPoints,
    })
    .then(
      res => res.data,
      error => {
        throw error;
      }
    );
}

export async function updateCameraToSite(siteId, camera, level, cameraPoints, floorPlanPoints) {
  const api = new API();
  return api
    .put(`${SITES}/${siteId}/addCamera`, {
      siteId,
      camera,
      level,
      cameraPoints,
      floorPlanPoints,
    })
    .then(
      res => res.data,
      error => {
        throw error;
      }
    );
}

export async function deleteCameraFromLevel(siteId, cameraId, levelId) {
  const api = new API();
  return api.delete(`${SITES}/${siteId}/addCamera/${cameraId}`, levelId).then(
    res => res.data,
    error => {
      throw error;
    }
  );
}

export async function updateSite(site, id) {
  const api = new API();
  return api.put(`${SITES}/${id}`, site).then(
    res => res.data,
    error => {
      throw error;
    }
  );
}

export async function deleteSite(id) {
  const api = new API();
  return api.delete(SITES, id).then(
    res => res.data,
    error => {
      throw error;
    }
  );
}
